// 处理月日,前面加 0
export function formatNumber(n) {
    n = n.toString();
    return n[1] ? n : "0" + n;
}

// 返回 以 tips 分割的 时间格式  0000/00/00
export function formatDate(date, tips) {
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    return [year, month, day].map(formatNumber).join(tips);
}

// 返回 以 tips 分割的 时间格式 0000/00/00 00:00:00
export function formatDate2(date, tips) {
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();

    const hour = date.getHours()
    const minute = date.getMinutes()
    const second = date.getSeconds()

    return [year, month, day].map(formatNumber).join(tips) + ' ' + [hour, minute, second].map(formatNumber).join(':');
}

// 返回 以 tips 分割的 时间格式 0000/00/00 00:00
export function formatDate3(date, tips) {
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();

    const hour = date.getHours()
    const minute = date.getMinutes()
    const second = date.getSeconds()

    return [year, month, day].map(formatNumber).join(tips) + ' ' + [hour, minute, second].map(formatNumber).join(':');
}

// 返回 以 tips 分割的 时间格式  00/00
export function formatDate4(date, tips) {
    const month = date.getMonth() + 1;
    const day = date.getDate();
    return [month, day].map(formatNumber).join(tips);
}

// 返回 当天 前第 n 天的时间格式 0000/00/00
export function pastDate(n, tips) {
    const date = new Date();
    const pastDate = new Date(date.getTime() - (24 * 60 * 60 * 1000) * n);
    const year = pastDate.getFullYear();
    const month = pastDate.getMonth() + 1;
    const day = pastDate.getDate();

    return [year, month, day].map(formatNumber).join(tips);
}

// 返回 两个日期之间的所以日期
export function dateArray(startDate, endDate) {
    const start_Date = startDate.split("-");
    const end_Date = endDate.split("-");
    const begin = new Date();
    begin.setUTCFullYear(start_Date[0],start_Date[1] - 1,start_Date[2])
    const end = new Date();
    end.setUTCFullYear(end_Date[0],end_Date[1] - 1,end_Date[2])
    const startTime = begin.getTime() - 24 * 60 * 60 * 1000;
    const endTime = end.getTime() - 24 * 60 * 60 * 1000;

    const dateArr = [];
    for (let n = startTime; n <= endTime;) {
        dateArr.push(this.formatDate4(new Date(parseInt(n)), '-'))
        n = n + 24 * 60 * 60 * 1000;
    }
    return dateArr;
}

export default {
    formatDate,
    formatDate2,
    formatDate3,
    formatDate4,
    pastDate,
    dateArray,
}