<template>
  <div id="app">
    <keep-alive :include="keepAliveList" exclude="notKeepAliveList">
      <router-view></router-view>
    </keep-alive>
    <AppFooter v-if="$route.meta.hasFooter" />
  </div>
</template>

<script>
import AppFooter from "./components/AppFooter.vue";
import routerConfig from "./router/routerConfig";

export default {
  name: "App",
  components: {
    AppFooter
  },
  data() {
    const that = this;
    return {
      pageTitle: ""
    };
  },
  watch: {
    $route(to, from) {
      // console.log(to);
    }
  },
  created() {
    //在页面加载时读取sessionStorage里的状态信息
    if (sessionStorage.getItem("store")) {
      this.$store.replaceState(
        Object.assign(
          {},
          this.$store.state,
          JSON.parse(sessionStorage.getItem("store"))
        )
      );
    }

    //在页面刷新时将vuex里的信息保存到sessionStorage里
    window.addEventListener("beforeunload", () => {
      sessionStorage.setItem("store", JSON.stringify(this.$store.state));
    });
  },
  computed: {
    keepAliveList: function() {
      // console.log(this.$store.getters.getKeepAliveList);
      // return this.$store.getters.getKeepAliveList.join();
      return routerConfig.keepAliveList.join();
    },
    notKeepAliveList: function() {
      // console.log(this.$store.getters.getNotKeepAliveList);
      // return this.$store.getters.getNotKeepAliveList.join();
      return routerConfig.notKeepAliveList.join();
    }
  },
  methods: {

  }
};
</script>

