import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { Toast } from 'vant'
import fastclick from 'fastclick'
import './assets/js/rem'//px转rem
import './assets/css/default.css'//全局默认样式
import http from './utils/config'//封装接口请求
import date from './utils/formatDate'//日期转换
import axios from 'axios';
Vue.prototype.$axios = axios;
import QS from 'qs'
Vue.prototype.qs = QS;
Vue.prototype.toast = Toast;
Vue.prototype.serverHost = http;
Vue.prototype.formatDate = date;

Vue.config.productionTip = false

fastclick.attach(document.body)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

//进入下个页面前的操作
router.beforeEach((to, from, next) => {
  // console.log('to')
  // console.log(to)
  // console.log('from')
  // console.log(from)
  //进入登录页面的时候清除 token
  // if(to.path === '/login' ){
  //   sessionStorage.removeItem("token", '');
  //   sessionStorage.removeItem("user_Data", '');
  // }

  if (to.meta.requireAuth) {  // 判断该路由是否需要登录权限
    if (store.state.token !== "" && store.state.token !== null) {  // 通过vuex state获取当前的token是否存在
      // console.log('需要登录授权')
      // router.push({ name: 'Login' });
      // next()
    } else {
      next()
    }
  } else {
    // console.log('不需要登录授权')
    next()
  }
})
