export default {
    setBaseURL(context, baseURL) {//自定义触发mutations里函数的方法，context与store 实例具有相同方法和属性
        context.commit("setBaseURL",baseURL);
    },
    setToken(context, token) {//自定义触发mutations里函数的方法，context与store 实例具有相同方法和属性
        context.commit("setToken",token);
    },
    setUserId(context, user_id) {
        context.commit("setUserId",user_id);
    },
    setStoreInfo(context, storeInfo) {
        context.commit("setStoreInfo",storeInfo);
    },
    setStoreInfoId(context, storeInfoId) {
        context.commit("setStoreInfoId",storeInfoId);
    },
    setDefaultDate(context, defaultDate) {
        context.commit("setDefaultDate",defaultDate);
    },

    // setKeepAliveList(context, keepAliveList) {
    //     context.commit("setKeepAliveList",keepAliveList);
    // },
    // setNotKeepAliveList(context, notKeepAliveList) {
    //     context.commit("setNotKeepAliveList",notKeepAliveList);
    // },
}