//getters可要，要不要，它主要就是方便了我们去取值操作 实时监听state值的变化(最新状态)
export default {
    //方法名随意,主要是来承载变化的 token 的值
    getBaseURL(state) {
        return state.baseURL;
    },
    getToken(state) {
        return state.token;
    },
    getUserId(state) {
        return state.user_id;
    },
    getStoreInfo(state) {
        return state.storeInfo;
    },
    getStoreInfoId(state) {
        return state.storeInfoId;
    },
    getDefaultDate(state) {
        return state.defaultDate;
    },

    // getKeepAliveList(state) {
    //     return state.keepAliveList;
    // },
    // getNotKeepAliveList(state) {
    //     return state.notKeepAliveList;
    // },
}
