<!--  -->
<template>
  <div class="banner-swipe">
    <van-swipe :autoplay="3000">
      <van-swipe-item v-for="(image, index) in images" :key="index">
        <img :src="image.imgUrl" />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import Vue from "vue";
import { Swipe, SwipeItem, Lazyload } from "vant";

Vue.use(Swipe)
  .use(SwipeItem)
  .use(Lazyload);

import banner01 from "@/assets/img/banner/banner01.jpg";
import banner02 from "@/assets/img/banner/banner02.jpg";
import banner03 from "@/assets/img/banner/banner03.jpg";
import banner04 from "@/assets/img/banner/banner04.jpg";

export default {
  data() {
    return {
      images: [
        { imgUrl: banner01 },
        { imgUrl: banner02 },
        { imgUrl: banner03 },
        { imgUrl: banner04 }
      ]
    };
  }
};
</script>

<style lang='scss'>
@import "../assets/css/font-color.scss";
.van-swipe-item{display: flex;align-items: center;}
.van-swipe__indicator{width: 6px;height: 6px;margin: 0 2px;border-radius: 6px;}
.van-swipe__indicator--active{width: 15px;background: $bg-color-main;}
</style>